const ProjectList = (props)=>{
    const [projectLoading, setProjectLoading] = useState(true)
    const [projectList, setProjectList] = useState([])

    const getProjects = async()=>{
        try {
            // TODO hardcode for test
            const result = await getInvestProjects('0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2', '0xC02aaA39b223FE8D0A0e5C4F27eAD9083C756Cc2', 0, 10)
            if (!result || !result.data) throw Error(result.error)

            const data = result.data.data
            const proj = data.projects
            // const { page, size, total } = proj
            const plist = proj.data.map(l=>{
                return {
                    name: l.name,
                    desc: l.intro,
                    img: `https://cloudflare-ipfs.com/ipfs/${l.logo}`
                }
            })
            setProjectList(plist)
            setProjectLoading(false)
        } catch (error) {
            console.error('getInvestProjects failed', error)
        }
    }

    useEffect(()=>{
        getProjects()
    }, [props.id])
    

    return (
        <Section>
            <div className="titleTop">Investment project</div>
            <Ul className='flex-lst font-light'>
                {
                    projectLoading ? (<LoadingNew></LoadingNew>) : projectList.map((item, i) => {
                        return (
                            <ProjectLi key={i}>
                                <ProjectImg>
                                    <img src={item.img} alt="" />
                                </ProjectImg>
                                <ProjectContent>
                                    <div className='title'>{item.name}</div>
                                    <div className='desc'>{item.desc}</div>
                                </ProjectContent>
                            </ProjectLi>
                        )
                    })
                }
                <ProjectLi>
                    <ProjectImg className='occupy-left'>
                        <img className='occupy' src={addProject} alt="" />
                    </ProjectImg>
                    <ProjectContent className='occupy-text'>
                        Upload Project
                    </ProjectContent>
                </ProjectLi>
            </Ul>
            
        </Section>
    )
}

const NFTlist = (props)=>{
    let ntf = {
        img: '',
        name: 'Chimera by mpkoz',
        desc: 'Chimera is a mutation,its genetics a merging of past and present.',
        price: 1111 
    }
    const { state } = useWeb3();
    const { venturecontract } = state;

    const [ntfList, setNtfList] = useState(new Array(8).fill(ntf))
    const [nftLoading, setNftLoading] = useState(true)

    const getNfts = async()=>{
        try {
            const nlist = []
            setNtfList(nlist)
            setNftLoading(false)
        } catch (error) {
            console.error('getNfts failed', error)
        }
    }

    useEffect(()=>{
        getNfts()
    }, [props.id, venturecontract])

    return (
        !nftLoading && ntfList.length > 0 && <NTFsection>
            <div className="container">
                <div className="titleTop">Investment NFT</div>
                <Ul className='flex-lst font-light'>
                {
                    nftLoading ? (<LoadingNew></LoadingNew>) : ntfList.map((item, i) => {
                        return (
                            <NTFli key={i}>
                                <img src={item.img} alt="" />
                                <NTFContent>
                                    <div className='name'>{item.name}</div>
                                    <div className='desc'>{item.desc}</div>
                                    <div className='price'>{item.price}</div>
                                </NTFContent>
                            </NTFli>
                        )
                    })
                }
                </Ul>
            </div>
            
        </NTFsection>
    )
}

