export default function VenturePanel(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { orgcontract, maincontract, whoAmI, venturecontract, daoContractAddress, vaultcontract} = state;

    const [id, setAId] = useState(null);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');

    const [fund, setFund] = useState({
        address: '',
        symbol: '',
        value: 0
    })
    const [mintToken, setMintToken] = useState({
        balance: 0,
        symbol: '',
    })

    const [manageFee, setManageFee] = useState(0)
    const [GpBonus, setGpBonus] = useState(0)


    useEffect(() => {
        setAId(props.match.params.id);
    }, [props.match.params.id]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);


    useEffect(async()=>{
        if (!id || !mainConnect || !venturecontract) return
        
        const getInvestBase = async()=>{
            // TODO check bonus meaning
            const bonus = await venturecontract.GPEarningBonus()
            setGpBonus(bonus.toString())
            const mfee = await venturecontract.managerFee()
            setManageFee(mfee.toString())
        }
        getInvestBase()
    }, [id, mainConnect, venturecontract])


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);


    const updateToken = (tokenList, fundData) => {
        console.log('fundData:' , fundData)
        const token = tokenList[0]
        setMintToken({
            symbol: token.symbol,
            balance: token.balance
        })
        setFund({
            symbol: fundData.symbol,
            address: fundData.address,
        })
    }

   
    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip className='font-light'>{errorTips}</Tip>
            </Modal>

            <div className='container'>
                <Left history={props.history} id={props.match.params.id} updateToken={updateToken} />
                <Section>
                    <div className="titleTop">Invest info</div>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>Mint Token</div>
                            <div className='value1'>{mintToken.balance}</div>
                            <div className='symbol'>{mintToken.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Fund Type</div>
                            <div className='value1'>{fund.address.slice(0, 6)}...{fund.address.slice(-6)}</div>
                            <div className='symbol'>{fund.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Manage Fee</div>
                            <div className='value2'>{manageFee}%</div>
                        </LI>
                        <LI>
                            <div className='title'>GP Bouns</div>
                            <div className='value2'>{GpBonus}%</div>
                        </LI>
                    </Ul>
                </Section>
                <YourShare mintTokenSymbol={mintToken.symbol} fundSymbol={fund.symbol}></YourShare>
                <ProjectList id={props.match.params.id}></ProjectList>
            </div>
            <NFTlist id={props.match.params.id}></NFTlist>
        </div>
    )
}
