export default function VentureConfig(props) {

    const { state, dispatch } = useWeb3();
    const { maincontract, daoManagercontract, web3Instance, Account } = state;

    const [admin, setAdmin] = useState(false);
    const [token, setToken] = useState(true);
    const [name, setname] = useState('');
    const [symbol, setsymbol] = useState('');
    const [supply, setsupply] = useState('');
    const [adminlist, setadminlist] = useState([
        {
            name: '',
            address: ''
        }
    ]);
    const [tokenlist, settokenlist] = useState([
        {
            address: '',
            token: ''
        }
    ]);
    const [addressList, setAddressList] = useState([{
        address: '',
        bonus: ''
    }])
    const [start, setstart] = useState(false);

    const [instanceByTemplate, setinstanceByTemplate] = useState(false);

    const [logo, setlogo] = useState('');
    const [daoname, setdaoname] = useState('');
    const [desc, setdesc] = useState('');
    const [ercUrl, setercUrl] = useState('');

    const [baseC, setbaseC] = useState(null);

    const [contractlist, setcontractlist] = useState(null);
    const [transfer, settransfer] = useState(false);

    const [queryAddrs, setqueryAddrs] = useState(false);
    const [next, setnext] = useState(false);
    const [daoinit, setDaoinit] = useState(false);
    const [adminstate, setadminstate] = useState(false);
    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');
    const [showDisable, setshowDisable] = useState(false);

    const [tokenAddress, setTokenAddress] = useState('')
    const [componentsList, setComponentsList] = useState(null)
    const [newDaoId, setNewDaoId] = useState(null);
    const [newDaoBase, setNewDaoBase] = useState({})

    const [configData, setConfigData] = useState({})
    const [selectToken, setSelectToken] = useState({})


    let { t } = useTranslation();

    const Submit = () => {
        // sessionStorage.setItem('forthStep', tokenAddress);
        toDataFormat()
    }

    const toThirdStep = () => {
        props.handlerSet(2);
        let form = {
            admin,
            token,
            name,
            symbol,
            supply,
            adminlist,
            tokenlist
        }
        sessionStorage.setItem('forthStep', JSON.stringify(form))
    }

    const addtoken = () => {

        let newArray = [...tokenlist];
        newArray.push({
            address: '',
            token: ''

        });
        settokenlist(newArray)
    }

    const setAddress = (e, index) => {
        let newArray = [...tokenlist];
        const { name, value } = e.target;
        newArray[index][name] = value;
        settokenlist(newArray)
    }

    const removeToken = (selectItem, index) => {
        let newArray = [...tokenlist];
        newArray.splice(index, 1);
        settokenlist(newArray)

    }

    const handleInput = (e) => {
        const { name, value } = e.target;
        if (name === 'GpCarry' || name === 'manageFee') {
            console.log(e.target.value, typeof e.target.value)
            if (Number(e.target.value) < 0 || Number(e.target.value) > 100) return
        }

        configData[name] = e.target.value
        setConfigData(configData)

        // switch (name) {
        //     case 'tokenAddress':
        //         setTokenAddress(e.target.value);
        //         break;
        //     case 'name':
        //         setname(e.target.value);
        //         break;
        //     case 'symbol':
        //         setsymbol(e.target.value);
        //         break;
        //     case 'supply':
        //         setsupply(e.target.value);
        //         break;
        //     default:
        //         break;
        // }

    }

    const handleCheck = (e) => {
        const { name, value } = e.target;
        switch (name) {
            case 'admin':
                setAdmin(!JSON.parse(value))
                break;
            case 'token':
                setToken(!JSON.parse(value))
                break;
            default:
                break;
        }
    }

    useEffect(() => {
        let form = JSON.parse(sessionStorage.getItem('forthStep'));
        if (!!form) {
            // setAdmin(form.admin);
            setToken(form.token);
            setname(form.name);
            setsymbol(form.symbol);
            setsupply(form.supply);
            // setadminlist(form.adminlist);
            settokenlist(form.tokenlist);
        }

        fundList.length > 0 && setSelectToken(fundList[0])
    }, []);

    // useEffect(() => {
    //     setshowDisable(!!tokenAddress)
    // }, [tokenAddress]);

    const toDataFormat = () => {
        const firstStep = JSON.parse(sessionStorage.getItem('firstStep'));
        const imgUrl = JSON.parse(sessionStorage.getItem('imgUrl'));

        if (firstStep != null) {
            setdaoname(firstStep.name);
            setdesc(firstStep.description);
            setlogo(imgUrl);
        }

        const secondStep = JSON.parse(sessionStorage.getItem('secondStep'));
        setComponentsList(secondStep)

        // const thirdStep = JSON.parse(sessionStorage.getItem('thirdStep'));
        // const {admin,adminlist} = thirdStep;

        setadminstate(false);
        // if(admin){
        //     setadminlist(adminlist);
        // }

        setstart(true)
    }




    useEffect(() => {
        if (!start) return;
        const initDao = async ()=>{
            dispatch({ type: 'LOADINGTYPE', payload: t('InstanceByTemplate') });
            console.log('VenturesManager: ', configData.GpCarry, configData.manageFee)
            console.log('** addressList: ', addressList.map(a=>a.address), addressList.map(a=>Number(a.bonus)))

            try {
                const signer = web3Instance.getSigner(Account)
                const res = await maincontract.connect(signer).instanceByTemplate(
                    [daoname, logo, desc], 
                    "VentureTemplate", 
                    [
                        "AuthManager", 
                        "VaultManager",
                        "VenturesManager", 
                        "GovTokenManager",
                    ],
                    [
                        "0x", 
                        // "VaultManager",
                        ethers.utils.defaultAbiCoder.encode(
                            ['address'],
                            [selectToken.address]
                        ),
                        // VentureManager
                        ethers.utils.defaultAbiCoder.encode(
                            ['uint256', 'uint256', 'uint256', 'uint256'],
                            [ethers.BigNumber.from(configData.GpCarry), ethers.BigNumber.from(configData.manageFee), ethers.BigNumber.from(configData.softCap), ethers.BigNumber.from(configData.hardCap)]
                        ),
                        // GovTokenManager
                        ethers.utils.defaultAbiCoder.encode(
                            // string memory name, string memory symbol, uint256 supply, address owner
                            ['string', 'string', 'uint256', 'address'],
                            [configData.tokenName, configData.tokenSymbol, 0, Account]
                        ),
            
                    ]
                );
                const r = await res.wait();
                const event = r.events.filter(e=>e.event === 'InstanceDAO')[0]
    
                const newDaoId = event.args[0].toString()
                setNewDaoId(newDaoId)
            } catch (error) {
                console.error(error)
                seterrorShow(true)
                seterrorTips(`init dao failed ${error && error.data && error.data.message}`)
                dispatch({ type: 'LOADINGTYPE', payload: null });
                setstart(false);
            }
        }
        initDao()
        
    }, [maincontract, start]);

    useEffect(async()=>{
        if (!newDaoId) return
        // add VoteManager component
        dispatch({ type: 'LOADINGTYPE', payload: 'Add Vote Manager' });

        const addVote = async()=>{
            try {
                const signer = web3Instance.getSigner(Account)
                const res = await maincontract.connect(signer).addComponent(newDaoId, "VoteManager", "0x")
                await res.wait()
            } catch (error) {
                console.error(error)
                seterrorShow(true)
                seterrorTips(`add vote manager failed ${error.data.message}`)
                dispatch({ type: 'LOADINGTYPE', payload: null });
                setstart(false);
            }
        }
        const getDao = async()=>{
            try {
                const data = await maincontract.instanceMap(parseInt(newDaoId));
                setNewDaoBase(data)
            } catch (error) {
                console.error('instanceMap failed', error)
            }
        }
        await addVote()
        getDao()
    }, [newDaoId])

    const showError = (error)=>{
        console.error(error)
        seterrorShow(true)
        seterrorTips(`add token failed ${error.data.message}`)
        dispatch({ type: 'LOADINGTYPE', payload: null });
    }

    useEffect(async ()=>{
        if (Object.keys(newDaoBase).length === 0) return
        dispatch({ type: 'LOADINGTYPE', payload: 'Add Token' });
        const daoOrg = await api.daoOrg.getContract(web3Instance, newDaoBase.daoOrganization) 
        const signer = web3Instance.getSigner(Account)

        const addToken = async()=>{
            try {
                const templateConfigAddress = await daoOrg.templateConfig()
                const templateConfig = await api.template.getContract(web3Instance, templateConfigAddress)
                const gm = await templateConfig.labelComponent('GovTokenManager')
                await api.vault.addToken(daoOrg, signer, gm)
            } catch (error) {
                showError(error)
            }
        }
        await addToken()

        // add fund manager
        const addFundManager = async ()=>{
            try {
                
            } catch (error) {
                showError(error)
            }
        }
        await addFundManager()
        // add Gp manager
        const addGpManager = async ()=>{
            try {
                const res = await maincontract.connect(signer).addComponent(newDaoId, "VenturesGPManager", ethers.utils.defaultAbiCoder.encode(
                    ['address[]', 'uint256[]'],
                    [addressList.map(a=>a.address), addressList.map(a=>ethers.BigNumber.from(a.bonus))]
                ))
                await res.wait()
                setnext(true);
            } catch (error) {
                showError(error)
            }
        }
        await addGpManager()

    }, [newDaoBase])

    useEffect(()=>{
        if (!next) return
        sessionStorage.removeItem("step");
        sessionStorage.removeItem("secondStep");
        sessionStorage.removeItem("firstStep");
        sessionStorage.removeItem("ImageUrl");
        sessionStorage.removeItem("forthStep");
        dispatch({ type: 'LOADINGTYPE', payload: null });
        props.history.push(`/about/${newDaoId}`);
        dispatch({ type: 'RELOAD_DAOS', payload: true });
    }, [next])

    const addAddress = ()=>{
        let newArray = [...addressList];
        newArray.push({
            address: '',
            bonus: ''

        });
        setAddressList(newArray)
    }

    const handleSetAddress = (e, index)=>{
        let newArray = [...addressList];
        const { name, value } = e.target;
        newArray[index][name] = value;
        setAddressList(newArray)
    }

    const handleSelectToken = (value) => {
        setSelectToken(fundList[value])
    }




    return <Content className="content">

        <Modal
            visible={errorShow}
            onCancel={() => seterrorShow(false)}
            footer={null}
        >
            <Tip>{errorTips}</Tip>
        </Modal>
        <Info>
            <div className="title">Template Configuration</div>
        </Info>

        <Translation>{t =>
            <div title={t('Token')} >
                <div className="line">
                    <div className='title'>Venture info</div>
                    <div className='line-block'>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>GP Carry</Trans></div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder="Please enter percentage"
                                    value={configData.GpCarry}
                                    name='GpCarry'
                                    type="number"
                                    checked={configData.GpCarry}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                                <span className='suffix'>%</span>
                            </div>
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>Manage fee</Trans></div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder="Please enter percentage"
                                    value={configData.manageFee}
                                    name='manageFee'
                                    type="number"
                                    checked={configData.manageFee}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                                <span className='suffix'>%</span>
                            </div>
                        </div>
                    </div>
                    
                </div>

                <div className="line line2">
                    <div className='title'>GP info</div>
                    <div className="subtitle font-light"><Trans>Address</Trans></div>
                    {
                        addressList.map((item, idx) => {return (
                            <div className='line-block address-item' key={idx}>
                                <div className='inputBrdr address-block'>
                                    <Input
                                        placeholder="Please enter"
                                        value={addressList[idx].address}
                                        name='address'
                                        autoComplete="off"
                                        onChange={(event) => handleSetAddress(event, idx)} />
                                </div>
                                <div className='inputBrdr bonus-block'>
                                    <Input
                                        placeholder="Bonus"
                                        value={addressList[idx].bonus}
                                        name='bonus'
                                        type="number"
                                        autoComplete="off"
                                        onChange={(event) => handleSetAddress(event, idx)} />
                                    <span className='suffix'>%</span>
                                </div>
                                 
                            </div>
                        )})
                        
                    }
                    <button className='btn-add font-light' onClick={addAddress}>Add More</button>


                </div>

                <div className="line line3">
                    <div className='title'>Found info</div>
                    <div className='line-block'>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>Token type</Trans></div>
                            <SelectBrdr style={{ width: '100%' }} onChange={handleSelectToken} placeholder={t('SelectOption')}>
                                {
                                    fundList.map((item, i) =>
                                        <Select.Option value={i} key={item.address}>
                                            <div style={{ display:'flex', alignItems:'center', height: '100%', lineHeight: '4.4rem'}}>
                                                <div style={{ display:'flex', flexDirection:'row', flexGrow:1}}>
                                                    <div style={{lineHeight: '3.55rem', fontWeight: 400, textTransform:'uppercase', marginRight: '0.5rem'}}>{item.symbol}</div>
                                                    <div style={{color: '#A6A6B7', lineHeight: '3.55rem'}}>{`${item.address.slice(0,6)}${item.address.slice(-6)}`}</div>
                                                </div>
                                            </div>
                                        </Select.Option>
                                    )
                                }
                            </SelectBrdr>
                            
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>Fund soft cap</Trans></div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder="Please enter"
                                    value={configData.softCap}
                                    name='softCap'
                                    type="number"
                                    checked={configData.softCap}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                                    <span className='suffix'>{selectToken.symbol}</span>
                            </div>
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>Fund hard cap</Trans></div>
                            <div className='inputBrdr'>
                                <Input
                                    className='input-without'
                                    placeholder="Please enter"
                                    value={configData.hardCap}
                                    name='hardCap'
                                    type="number"
                                    checked={configData.hardCap}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                                    <span className='suffix'>{selectToken.symbol}</span>
                            </div>
                        </div>
                        
                    </div>
                    <div className='line-block'>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>Token name</Trans></div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder="Please enter token name"
                                    value={configData.tokenName}
                                    name='tokenName'
                                    checked={configData.tokenName}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                        <div className='block'>
                            <div className="subtitle font-light"><Trans>Token symbol</Trans></div>
                            <div className='inputBrdr'>
                                <Input
                                    placeholder="Please enter token symbol"
                                    value={configData.tokenSymbol}
                                    name='tokenSymbol'
                                    checked={configData.tokenSymbol}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                    </div>
                    
                </div>
                
                <div className='tip'>
                    <div className='img-box'><img src={WranImg} alt="" /></div>
                    <div className='tip-text'>When you complete the creation , you will enter the subscription period and start foud raising . The successful fundraising will automatically enter the lock-up period. When the closed will end by GP voting. After that, LP can collect the principal and profits.</div>
                </div>
               
            </div>

        }
        </Translation>

        <div className="line" style={{ textAlign: 'right', marginTop: '4rem' }}>
            <Button className='previous' style={{ marginRight: '3rem' }} onClick={()=>toThirdStep()}>
                <img className="left" src={left} alt="" />
                <Trans>think</Trans>
            </Button>
            {/* <Button type="primary" onClick={()=>Submit()} className="nextBtn"  disabled={!showDisable}> */}
            <Button type="primary" onClick={()=>Submit()} className="nextBtn">
                Submit
                <img src={right} alt="" />
            </Button>
        </div>

    </Content>;

}
