export default function Venture(props) {
    let { t } = useTranslation();

    const { state, dispatch } = useWeb3();
    const { venturecontract, maincontract, whoAmI, web3Instance} = state;

    const [id, setAId] = useState(null);

    const [isMember, setisMember] = useState(false);
    const [isModerator, setisModerator] = useState(false);
    const [isOwner, setisOwner] = useState(false);

    const [errorShow, seterrorShow] = useState(false);
    const [errorTips, seterrorTips] = useState('');

    const [buyShow, setBuyShow] = useState(false);
    const [raiseGp, setRaiseGp] = useState(false);
    const [raiseLp, setRaiseLp] = useState(false);
    const [whitelist, setWhitelist] = useState(false);
    const [deposit, setDeposit] = useState(false);
    const [withdraw, setWithdraw] = useState(false);
    const [confirmShow, setConfirmShow] = useState(false);
    const [confirmTips, setConfirmTips] = useState('');
    const [tokenBuy, setTokenbuy] = useState(false);

    const [raiseInfo, setRaiseInfo] = useState({
        minToken: {
            balance: 111,
            symbol: 'SDV',
        },
        manageFee: {
            value: 1,
        },
        bonus: {
            value: 1,
        },
        fund: {
            address: '0xaa660cceFA711aEEa78C747E49F309952bcE2367',
            symbol: 'USDT',
            value: 0
        },
        goal: 10000
    })

    const [investInfo, setInvestInfo] = useState({
        token: {
            value: 11111,
            symbol: 'SDV',
        },
        deposit: {
            value: 11111,
            symbol: 'USDT',
        }
    })

    const [tokenInfo, setTokenInfo] = useState({
        contract: '',
        id: ''
    })

    useEffect(() => {
        setAId(props.match.params.id);
    }, [props.match.params.id]);

    useEffect(async () => {
        if(maincontract == null) dispatch({ type: 'LOAD_MAINCONTRACT' });
    }, []);

    useEffect(async()=>{
        if (!id || !mainConnect) return
    }, [id, mainConnect])


    useEffect(() => {
        if (whoAmI ==null) return;
        setisOwner(whoAmI[0]);
        setisModerator(whoAmI[1]);
        setisMember(whoAmI[2]);
    }, [id, maincontract, whoAmI]);

    useEffect(()=>{
        if (!venturecontract) return
    }, [venturecontract])


    const handleInput = (e) => {
        const { name } = e.target;
        tokenInfo[name] = e.target.value
        setTokenInfo(tokenInfo)
    }

    const clickSearch = ()=>{

    }
    const clickTokenBuy = ()=>{
        setTokenbuy(true)
    }

    const CloseTokenBuy = () =>{
        setTokenbuy(false)
    }
    const clickRaise = ()=>{
        setRaiseGp(true)
    }
    const closeRaise = ()=>{
        setRaiseGp(false)
    }
    const clickRaiseLp = ()=>{
        setRaiseLp(true)
    }
    const closeRaiseLp = ()=>{
        setRaiseLp(false)
    }
    const clickWhitelist = ()=>{
        setWhitelist(true)
    }
    const closeWhitelist = ()=>{
        setWhitelist(false)
    }
    const clickDeposit = ()=>{
        setDeposit(true);
    }

    const closeDeposit = ()=>{
        setDeposit(false)
    }
    const clickWithdraw = ()=>{
        setWithdraw(true);
    }
    const clickTokenBuyModal = ()=>{
        
    }
    const closeTokenBuyModal = ()=>{
        
    }

    const closeWithdraw = ()=>{
        setWithdraw(false)
    }
    const clickCloseVenture = ()=>{
        setConfirmShow(true);
        setConfirmTips('Close Venture');
    }
    const CloseShow = ()=>{
        setConfirmShow(false);
    }

    const clickRefund = ()=>{
        setConfirmShow(true);
        setConfirmTips('Refund');
    }

    const updateToken = (tokenList, fund) => {
        raiseInfo.minToken = tokenList[0]
        raiseInfo.fund.symbol = fund.symbol
        raiseInfo.fund.address = fund.address
        setRaiseInfo(raiseInfo)
    }

    return (
        <div>
            <Modal
                visible={errorShow}
                onCancel={() => seterrorShow(false)}
                footer={null}
            >
                <Tip>{errorTips}</Tip>
            </Modal>


            <div className='container'>
                <Left history={props.history} id={props.match.params.id} updateToken={updateToken} />
                <Section>
                    <div className="titleTop">Raise info</div>
                    <Period className='font-light'>Raise period</Period>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>Mint Token</div>
                            <div className='value1'>{raiseInfo.minToken.balance}</div>
                            <div className='symbol'>{raiseInfo.minToken.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Fund Type</div>
                            <div className='value1'>{raiseInfo.fund.address.slice(0, 6)}...{raiseInfo.fund.address.slice(-6)}</div>
                            <div className='symbol'>{raiseInfo.fund.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Raised Fund</div>
                            <div className='value1'>{raiseInfo.fund.value}</div>
                            <div className='symbol'>{raiseInfo.fund.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Raised Goal</div>
                            <div className='value1'>{raiseInfo.goal}</div>
                            <div className='symbol'>{raiseInfo.fund.symbol}</div>
                        </LI>
                    </Ul>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>Manage Fee</div>
                            <div className='value2'>{raiseInfo.manageFee.value}%</div>
                        </LI>
                        <LI>
                            <div className='title'>GP Bouns</div>
                            <div className='value2'>{raiseInfo.bonus.value}%</div>
                        </LI>
                    </Ul>
                </Section>
                <Section>
                    <div className="titleTop">Invest info</div>
                    <Ul className='font-light'>
                        <LI>
                            <div className='title'>Your Token</div>
                            <div className='value1'>{investInfo.token.value}</div>
                            <div className='symbol'>{investInfo.token.symbol}</div>
                        </LI>
                        <LI>
                            <div className='title'>Your Deposit</div>
                            <div className='value1'>{investInfo.deposit.value}</div>
                            <div className='symbol'>{investInfo.deposit.symbol}</div>
                        </LI>
                    </Ul>
                </Section>
                <Section>
                    <div className="titleTop">Investment</div>
                    <InvestSection className='font-light'>
                        <div>
                            <div className="input-title">Token contract</div>
                            <div className='inputBrdr'>
                                <Input
                                    className='gray-input'
                                    placeholder="Please enter"
                                    value={tokenInfo.contract}
                                    name='contract'
                                    checked={tokenInfo.contract}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                        <div>
                            <div className="input-title">Token id</div>
                            <div className='inputBrdr'>
                                <Input
                                    className='gray-input'
                                    placeholder="Please enter"
                                    value={tokenInfo.id}
                                    name='id'
                                    checked={tokenInfo.id}
                                    onChange={handleInput}
                                    autoComplete="off"/>
                            </div>
                        </div>
                   
                        <div className='btn-search '>
                            <NButton className='light' onClick={() => clickSearch()}>
                                <img src={Search} alt="" />
                                Search
                            </NButton>
                        </div>
                    </InvestSection>

                </Section>
                <BuyModal show={buyShow} CloseTokenBuy={CloseTokenBuy} />
                <Section>
                    <div className="titleTop">Token buy</div>
                    <NButton className='light font-light' onClick={() => clickTokenBuy()}>Click me to buy</NButton>
                </Section>
                <Section>
                    <RaiseGps show={raiseGp} closeRaise={()=>closeRaise()} symbol={raiseInfo.fund.symbol}/>
                    <NButton onClick={() => clickRaise()}>Raise</NButton>



                    <RaiseLps show={raiseLp} closeRaise={()=>closeRaiseLp()} symbol={raiseInfo.fund.symbol}/>
                    {/*<NButton onClick={() => clickRaiseLp()}>Raise Lp</NButton>*/}

                    <Whitelist show={whitelist} closeRaise={()=>closeWhitelist()}/>

                    {/*<NButton onClick={() => clickWhitelist()}>Lp White List</NButton>*/}


                    <Deposit show={deposit} closeDeposit={()=>closeDeposit()} showContent={true}/>
                    <NButton onClick={() => clickDeposit()}>Deposit</NButton>

                    <Withdraw show={withdraw} closeWithdraw={()=>closeWithdraw()} />
                    <NButton className='pirmary' onClick={() => clickWithdraw()}>Withdraw</NButton>

                    <TokenBuy show={tokenBuy} closeTokenBuy={()=>closeTokenBuyModal()} />

                    <ConfirmBox show={confirmShow} handleClose={()=>CloseShow()} tips={confirmTips} />
                    <NButton className='pirmary' onClick={() => clickCloseVenture()}>Close venture</NButton>
                    <NButton className='pirmary' onClick={() => clickRefund()}>Refund</NButton>
                </Section>
            </div>
        </div>
    )
}
