import React, { useEffect, useState } from "react";
import {Button, Input, Modal, DatePicker} from "antd";
import styled from "styled-components";
import moment from 'moment';
import { useWeb3 } from "../../api/contracts";
import Estimate from "./estimate";


const ModalBox = styled(Modal)`
   .ant-input{
      font-size: 1.8rem!important;
    }
    .ant-modal-body{
      max-height: 70rem;
      overflow-y: auto;
    }
    .title,.label{
     font-family: "Roboto-Light"!important;
    }
`

const NextBrdr = styled.div`
  .default{
    background: #FFFFFF;
    border-radius: 1.2rem;
    border: 0.15rem solid #D52473;
      
color: #D52473;
  }
`


export default function RaiseGps(props) {
    const { show ,closeRaise } = props;

    const { state, dispatch } = useWeb3();
    const { ventrurecontract, web3Instance } = state;

    const [ratio, setRatio] = useState(null)
    const [maxDep, setMaxDep] = useState(null)
    const [minDep, setMinDep] = useState(null)

    const [raiseTime, setRaiseTime] = useState(null)

    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }

    const handleInputChange = (e) =>{
        const {name, value} = e.target
        switch (name) {
            case 'ratio':
                setRatio(value)
                break
            case 'maxDep':
                setMaxDep(value)
                break
            case 'minDep':
                setMinDep(value)
                break
            default:
                return
        }
    }

    const handleRaiseTime = (v)=>{
        const dateTime = Date.parse(v._d)
        setRaiseTime(dateTime)
    }

    const handleClose = () =>{
        closeRaise()
    }

    const confirmRaise = ()=>{
        console.log("~confirmRaise~")
    }

    return <ModalBox visible={show} onCancel={handleClose} footer={null} destroyOnClose={true} maskClosable={false}>
        <div className="title">
            <span >Raise for GPs</span>
        </div>

        <div className="label">Raise time</div>
        <div className="inputBrdr">
            <DatePicker
                className="myDatePicker"
                placeholder='Please choose raise time'
                style={{ width: '100%' }}
                format="YYYY-MM-DD HH:mm:ss"
                allowClear={false}
                disabledDate={disabledDate}
                onChange={handleRaiseTime}
                showTime={true}
                showNow={false} />
        </div>
        <Estimate time={raiseTime} />
            
        <div className="label">Raise ratio</div>
        <div className="inputBrdr">
            <Input
                placeholder='ratio'
                name='ratio'
                value={ratio}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>%</span>
        </div>

        <div className="label">Max deposit per GP</div>
        <div className="inputBrdr">
            <Input
                placeholder='Max deposit per GP'
                name='maxDep'
                value={maxDep}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>{props.symbol}</span>
        </div>
        <div className="label">Min deposit per GP</div>
        <div className="inputBrdr">
            <Input
                placeholder='Min deposit per GP'
                name='minDep'
                value={minDep}
                autoComplete="off"
                type="number"
                onChange={(e)=>handleInputChange(e)}
            />
            <span className='suffix'>{props.symbol}</span>
        </div>

        <NextBrdr >
            <Button type="primary"  style={{ width: '100%', marginTop: '3rem' }} onClick={confirmRaise}>
                Submit
            </Button>
        </NextBrdr>
    </ModalBox>
}
